/* Status bar fill with moving gradient effect */
.status-bar-fill {
  height: 10px;
  background: linear-gradient(to right, #F76C0F, #9E2E07, #F76C0F);


  /*background: linear-gradient(to right, #F76C0F, #E9500D, #F76C0F); */
  background-size: 200% 100%; /* Makes the gradient larger than the container */
  transition: width 1s ease-out;
  width: 0;
  border-radius: 15px; /* Ensure border-radius matches the container */
  /*animation: gradientShift 1.30s linear infinite; */
  animation: gradientPush 6s linear infinite;
}

/* Keyframes for moving gradient effect with pause */
@keyframes gradientPush {
  0% {
    background-position: 0% 0%;
  }
  50% {
    background-position: 100% 0%;
  }
  100% {
    background-position: 0% 0%;
  }
}










/* Pseudo-element to display and animate the logo */
.status-bar-fill::before {
  content: '';
  position: absolute;
  top: 75%; /* Position the logo right below the bottom of the .status-bar-fill */
  left: 46%; /* Center horizontally relative to the parent */
  transform: translate(-50%, 0); /* Center horizontally by adjusting its own width */
  width: 70px; /* Set width of the logo */
  height: 70px; /* Set height of the logo */
  background-image: url('./ubuntu1.png'); /* Path to your image */
  background-repeat: no-repeat;
  background-size: contain; /* Ensure the image maintains its aspect ratio */
  background-position: center; /* Center the image */
  opacity: 0.8; /* Adjust opacity as needed to see both the logo and gradient */
  pointer-events: none; /* Ensure the logo does not interfere with user interactions */
  animation: spin 9s linear infinite; /* Apply the spinning animation */
}

/* Keyframes for spinning animation */
@keyframes spin {
  0% {
    transform: rotate(0deg); /* Start from 0 degrees */
  }
  100% {
    transform: rotate(360deg); /* Rotate to 360 degrees */
  }
}









.log-success {
  color: #28a745;
  font-weight: bold;
  display: flex;
  align-items: center;
  opacity: 0; /* Start with hidden */
  transform: scale(0.9) translateY(-10px); /* Start with a slightly smaller scale */
  margin-top: 50px;
  position: relative;
  overflow: hidden; /* Hide overflow to keep the effect contained */
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%); /* Start with full visibility */
}

/* Keyframes for initial fade-in */
@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: scale(0.9) translateY(-10px);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

/* Keyframes for broken glass effect */
@keyframes glassBreak {
  0% {
    opacity: 1;
    transform: scale(1) translateY(0);
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
  25% {
    opacity: 1;
    transform: scale(1.05) translateY(-5px);
    clip-path: polygon(0 0, 100% 0, 90% 100%, 10% 100%); /* Shatter effect */
  }
  50% {
    opacity: 1;
    transform: scale(1) translateY(0);
    clip-path: polygon(0 0, 100% 0, 80% 100%, 20% 100%); /* More shattered effect */
  }
  75% {
    opacity: 1;
    transform: scale(1) translateY(0);
    clip-path: polygon(0 0, 100% 0, 70% 100%, 30% 100%); /* Final shattered effect */
  }
  100% {
    opacity: 0; /* Fade out completely */
    transform: scale(0.9) translateY(-10px); /* Final hidden state */
    clip-path: polygon(0 0, 100% 0, 50% 100%, 50% 100%); /* Final clipped state */
  }
}

/* Animation class for showing and then breaking */
.animate-success {
  animation: fadeIn 0.7s ease-out forwards, glassBreak 0.7s ease-out forwards 4s; /* Delay glassBreak by 4s */
}









/* CustomTitleBar.css */
.title-bar {
  background-color: #E9500D; /* Change to your desired color */
  height: 30px;
  -webkit-app-region: drag;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
}

.title-bar-drag-region {
  -webkit-app-region: drag;
  flex-grow: 1;
}

.title-bar-buttons button {
  -webkit-app-region: no-drag;
  background: none;
  border: none;
  color: white;
  font-size: 16px;
  margin-left: 10px;
  cursor: pointer;
}

.title-bar-buttons button:hover {
  background-color: rgba(255, 255, 255, 0.1);
}












